import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { ServiceComponent } from './service/service.component';
import { ProjectComponent } from './project/project.component';
import { ContactComponent } from './contact/contact.component';
import { SliderComponent } from './slider/slider.component';
import { ClientAddFormComponent } from './client-add-form/client-add-form.component';
import { SliderAddFormComponent } from './slider-add-form/slider-add-form.component';
import { ModalComponent } from './modal/modal.component';

const routes: Routes = [
  {path : 'home' , component:HomeComponent},
  {path : 'about' , component:AboutComponent},
  {path : 'service' , component:ServiceComponent},
  {path : 'portfolio' , component:ProjectComponent},
  {path : 'contact' , component:ContactComponent},
  {path : 'slider' , component:SliderComponent},
  {path : 'admin-client' , component:ClientAddFormComponent},
  {path : 'admin-slider' , component:SliderAddFormComponent},
  {path : 'modal' , component:ModalComponent},
  {path: '', redirectTo: 'home', pathMatch: 'full' },
  {path: '**', redirectTo: 'service', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
