import { HttpClient, HttpEventType, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Post,Slider } from './post.model';
import { map,tap } from 'rxjs/operators';

@Injectable({
    providedIn:'root'
})
export class PostsService{
    
    constructor(private http : HttpClient){}

    createStoreandPost(client_name:string, client_image_path:string){
        const postData:Post = {client_name:client_name, client_image_path:client_image_path};
        this.http
        .post<{name:string; client_name:string; client_image_path:string;}>(
          'https://krm-info-tech-default-rtdb.firebaseio.com/posts.json',
          postData,{
            observe : 'response'
          }
        )
        .subscribe(responseData => {
          console.log(responseData);
        });

        client_name = '';
        client_image_path = '';
  
    }

    fetchPost(){
        // let SearchParams = new HttpParams();
        // SearchParams = SearchParams.append('test','bow');
        // SearchParams = SearchParams.append('Mohamed','Hasim');
        return this.http
        .get<{[key:string] : Post }>('https://krm-info-tech-default-rtdb.firebaseio.com/posts.json', 
        {
         headers:new HttpHeaders({'Custem_Header' : 'Hello'}),
        //  params : new HttpParams().set('mohame','nid')
        // params : SearchParams,
        responseType:'json'
        }
        )
        .pipe(
          map(responseData => {
            const postsArray:Post[] = [];
            for (const key in responseData) {
              if (responseData.hasOwnProperty(key)) {
                postsArray.push({ ...responseData[key], id: key });
              }
            }
            return postsArray;
          })
        )
        
    }

    deletePosts(){
       return this.http.delete('https://krm-info-tech-default-rtdb.firebaseio.com/posts.json',{
         observe:'events',
         responseType : 'text'
       }).pipe(tap( event=>{
         
        if(event.type === HttpEventType.Sent){
          // console.log(event.body); ..../
        }
         console.log(event);
         if(event.type === HttpEventType.Response){
           console.log(event.body);
         }
       })
       
       );
    }
    

    // slider create post 

    createPostSlider(sliderDataFormat : Slider){
      this.http.post('https://krm-info-tech-default-rtdb.firebaseio.com/slider.json',
      sliderDataFormat).subscribe( responseDataSlider =>{
        console.log(responseDataSlider);
      });
    }
   

    // slider data fetch
    
    fetchSlider(){
      // let SearchParams = new HttpParams();
      // SearchParams = SearchParams.append('test','bow');
      // SearchParams = SearchParams.append('Mohamed','Hasim');
      return this.http
      .get('https://krm-info-tech-default-rtdb.firebaseio.com/slider.json')
      .pipe(
        map(responseData => {
          const sliderArraynew = [];
          for (const keyslider in responseData) {
            if (responseData.hasOwnProperty(keyslider)) {
              sliderArraynew.push({ ...responseData[keyslider], id: keyslider });
            }
          }
          return sliderArraynew;
        })
      )
      
  }
  // fetch end


  // delete slider 

  deleteSlider(){
   return this.http.delete('https://krm-info-tech-default-rtdb.firebaseio.com/slider.json')
  }

  // delete slider end
}