import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { PostsService } from '../shared/posts.service';
import { Slider } from '../shared/post.model';


@Component({
  selector: 'app-slider-add-form',
  templateUrl: './slider-add-form.component.html',
  styleUrls: ['./slider-add-form.component.css']
})
export class SliderAddFormComponent implements OnInit {

  sliderData : Slider[] = [];
  isFatching = false;
  error = null;

  constructor(private http: HttpClient, private postsSerivce : PostsService) {}

  ngOnInit() {
    this.isFatching=true;
    this.postsSerivce.fetchSlider().subscribe(slider=>{
      this.isFatching=false;
      this.sliderData = slider;
    }, error=>{
      this.error = error.message;
      console.log(error);
   });
  }

  //new
 // model: Signup = new Signup();

  @ViewChild('sliderForm') form: any;

  //end


  modelSlider = {
    sliderID : '',
    sliderName : '',
    sliderDesc : '',
    sliderImgPath : ''
  }

  onCreateSlider(form : any){
    this.postsSerivce.createPostSlider(form.value);
    this.form.reset();
  }


  onFetchPosts() {
    // Send Http request
    // this.isFatching = true;
    // this.postsSerivce.fetchPost();
    this.isFatching=true;
    this.postsSerivce.fetchSlider().subscribe(slider=>{
      this.isFatching=false;
      this.sliderData = slider;
    }, error=>{
      this.error = error.message;
      console.log(error);
   });
  }

  onClearSlider() {
    // Send Http request
    this.postsSerivce.deleteSlider().subscribe(()=>{
       this.sliderData = [];
    })
  }


}

class Signup {
  constructor(public clientName: string = '',
              public clientImgPath: string = '') {
  }
}