import { Component, OnInit } from '@angular/core';
import { Slider } from '../shared/post.model';
import { PostsService } from '../shared/posts.service';

@Component({
  selector: 'app-slider',
  templateUrl: './slider.component.html',
  styleUrls: ['./slider.component.css']
})
export class SliderComponent implements OnInit {
  sliderData : Slider[] = [];
  isFatching = false;
  error = null;

  constructor(private sliderService : PostsService) { }

  ngOnInit(): void {
    this.isFatching=true;
    this.sliderService.fetchSlider().subscribe(slider=>{
      this.isFatching=false;
      this.sliderData = slider;
      console.log(slider);
    }, error=>{
      this.error = error.message;
      console.log(error);
   });
  }

}
