import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';


import { Post } from '../shared/post.model';
import { PostsService } from '../shared/posts.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  loadedPosts : Post[] = [];
  isFatching = false;
  error = null;

  constructor(private http: HttpClient, private postsSerivce : PostsService) {}

  ngOnInit() {
    this.isFatching=true;
    this.postsSerivce.fetchPost().subscribe(posts=>{
      this.isFatching=false;
      this.loadedPosts = posts;
    }, error=>{
       this.error = error.message;
    });
  }

  onCreatePost(postData: { title: string; content: string }) {
    // Send Http request
    // this.http
    //   .post<{name:string;}>(
    //     'https://test-hasim-db-default-rtdb.firebaseio.com/posts.json',
    //     postData
    //   )
    //   .subscribe(responseData => {
    //     console.log(responseData);
    //   });
    this.postsSerivce.createStoreandPost(postData.title,postData.content)
    
  }

  onFetchPosts() {
    // Send Http request
    // this.isFatching = true;
    // this.postsSerivce.fetchPost();
    this.isFatching=true;
    this.postsSerivce.fetchPost().subscribe(posts=>{
      this.isFatching=false;
      this.loadedPosts = posts;
    }, error=>{
      this.error = error.message;
      console.log(error);
   });
  }

  onClearPosts() {
    // Send Http request
    this.postsSerivce.deletePosts().subscribe(()=>{
       this.loadedPosts = [];
    })
  }


}
